'use client'

import { useState, useEffect } from "react"
import { useMutation, useQuery } from "@apollo/client"
import { gql } from "@apollo/client"
import { DashboardLayout } from "@/components/dashboard/dashboard-layout"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { DashboardShell } from "@/components/dashboard-shell"
import { Search, MapPin, Filter, Calendar, Users, X } from "lucide-react"
import { Badge } from "@/components/ui/badge"
import { useSearchParams, useRouter } from "next/navigation"
import ProtectedRoute from "@/components/ProtectedRoute"
import { IS_LOGGED_IN } from "@/app/apollo/operations/auth"
import { 
  GetAllEvents,
  GET_EVENTS_NEAR_ME
} from "@/app/apollo/queries/events"
import {
  LIKE_EXTERNAL_EVENT,
  UNLIKE_EXTERNAL_EVENT,
  SAVE_EXTERNAL_EVENT,
  UNSAVE_EXTERNAL_EVENT,
  // Other mutations...
} from "@/app/apollo/mutations/eventsmutations"
import { EventCard } from "@/components/dashboard/eventcard"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Switch } from "@/components/ui/switch"
import { 
  Dialog, 
  DialogContent, 
  DialogHeader, 
  DialogTitle, 
  DialogDescription,
  DialogFooter
} from "@/components/ui/dialog"
import { format } from "date-fns"

// Create Event Mutation
const CREATE_EVENT_MUTATION = gql`
  mutation CreateEvent($data: CreateEventInput!) {
    createEvent(data: $data) {
      id
      name
      description
      category
      location
      startDate
      endDate
      capacity
      isPublic
      tags
      status
      creator {
        id
        firstName
      }
    }
  }
`;

const EVENTS_PER_PAGE = 6

export default function EventsPageWrapper() {
  return (
    <ProtectedRoute allowedRoles={['student', 'university', 'company', 'admin']}>
      <EventsPage />
    </ProtectedRoute>
  );
}

function EventsPage() {
  // Router and search params
  const searchParams = useSearchParams();
  const router = useRouter();
  const roleParam = searchParams.get('role');

  // Initialize all state variables at the top
  const [userRole, setUserRole] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState("");
  const [categoryFilter, setCategoryFilter] = useState("ALL");
  const [dateFilter, setDateFilter] = useState("ALL");
  const [userCoordinates, setUserCoordinates] = useState(null);
  const [nearMeActive, setNearMeActive] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [isLoadingLocation, setIsLoadingLocation] = useState(false);
  
  // Create event modal state
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [imageFile, setImageFile] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);
  const [formErrors, setFormErrors] = useState({});
  const [formData, setFormData] = useState({
    name: "",
    description: "",
    category: "",
    location: "",
    startDate: "",
    startTime: "10:00",
    endDate: "",
    endTime: "16:00",
    capacity: 50,
    isPublic: true,
    tags: "",
    perks: ""
  });

  // Get user data from Apollo cache - always call this hook
  const { data: authData, loading: authLoading } = useQuery(IS_LOGGED_IN);
  const currentUser = authData?.currentUser || null;
  const userId = currentUser?.id || null;
  
  // Helper function to get query variables - call this outside hook definitions
  const getQueryVariables = () => {
    return {
      filter: {
        name: searchTerm || undefined,
        category: categoryFilter === "ALL" ? undefined : categoryFilter,
        startDateAfter: dateFilter === "today" ? new Date().toISOString() :
                       dateFilter === "week" ? new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString() :
                       dateFilter === "month" ? new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString() :
                       undefined,
        includeInternalEvents: true,
        includeExternalEvents: true
      },
      orderBy: { field: "START_DATE", direction: "ASC" },
      skip: (currentPage - 1) * EVENTS_PER_PAGE,
      take: EVENTS_PER_PAGE,
      userId: userId
    };
  };

  // Always call all query hooks, but use skip parameter to conditionally fetch
  // Query to fetch all events - always call this hook
  const { loading, error, data, refetch } = useQuery(GetAllEvents, {
    variables: getQueryVariables(),
    fetchPolicy: 'cache-and-network',
    skip: !userId || nearMeActive // Skip if no user ID or using near me
  });

  // Query for near me events - always call this hook
  const { data: nearMeData, loading: nearMeLoading } = useQuery(GET_EVENTS_NEAR_ME, {
    variables: {
      latitude: userCoordinates?.latitude || 0,
      longitude: userCoordinates?.longitude || 0,
      radiusInKm: 10,
      skip: (currentPage - 1) * EVENTS_PER_PAGE,
      take: EVENTS_PER_PAGE,
      userId: userId,
      filter: {
        name: searchTerm || undefined,
        category: categoryFilter === "ALL" ? undefined : categoryFilter,
        startDateAfter: dateFilter === "today" ? new Date().toISOString() :
                       dateFilter === "week" ? new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString() :
                       dateFilter === "month" ? new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString() :
                       undefined,
      }
    },
    skip: !userCoordinates || !nearMeActive || !userId, // Skip if not using near me
    fetchPolicy: 'cache-and-network'
  });

  // Create event mutation
  const [createEvent, { loading: createLoading }] = useMutation(CREATE_EVENT_MUTATION, {
    onCompleted: (data) => {
      // Reset form and close modal
      resetForm();
      setIsCreateModalOpen(false);
      // Refetch events to show the new one
      refetch();
    },
    onError: (error) => {
      console.error("Error creating event:", error);
      setFormErrors({ 
        submit: error.message || "Failed to create event. Please try again." 
      });
    }
  });

  // Always define all mutations
  const [likeExternalEvent] = useMutation(LIKE_EXTERNAL_EVENT);
  const [unlikeExternalEvent] = useMutation(UNLIKE_EXTERNAL_EVENT);
  const [saveExternalEvent] = useMutation(SAVE_EXTERNAL_EVENT);
  const [unsaveExternalEvent] = useMutation(UNSAVE_EXTERNAL_EVENT);
  
  // Update role from cache and URL parameter
  useEffect(() => {
    if (authLoading) return;
    
    if (currentUser) {
      // Set whether the user is an admin
      setIsAdmin(currentUser.role.toLowerCase() === 'admin');
      
      // Determine the effective role
      let effectiveRole = currentUser.role.toLowerCase();
      
      // If role param exists and is valid, use it instead
      if (roleParam && ['student', 'university', 'company', 'admin'].includes(roleParam)) {
        effectiveRole = roleParam;
      } else if (roleParam) {
        // If role is invalid, redirect to the user's actual role
        router.replace(`/events?role=${effectiveRole}`);
        return;
      } else {
        // If no role provided, add the user's role to URL
        router.replace(`/events?role=${effectiveRole}`);
        return;
      }
      
      setUserRole(effectiveRole);
    }
  }, [authLoading, currentUser, roleParam, router]);

  // Refetch data when filters change
  useEffect(() => {
    if (!nearMeActive && userId) {
      refetch(getQueryVariables());
    }
  }, [categoryFilter, dateFilter, searchTerm, currentPage, nearMeActive, userId, refetch]);

  // Reset form state
  const resetForm = () => {
    setFormData({
      name: "",
      description: "",
      category: "",
      location: "",
      startDate: "",
      startTime: "10:00",
      endDate: "",
      endTime: "16:00",
      capacity: 50,
      isPublic: true,
      tags: "",
      perks: ""
    });
    setImageFile(null);
    setImagePreview(null);
    setFormErrors({});
  };

  // Handle image file selection
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  // Handle form input changes
  const handleInputChange = (e) => {
    const { id, value, type, checked } = e.target;
    
    setFormData(prev => ({
      ...prev,
      [id]: type === 'checkbox' ? checked : value
    }));
    
    // Clear errors for this field when user changes input
    if (formErrors[id]) {
      setFormErrors(prev => {
        const newErrors = {...prev};
        delete newErrors[id];
        return newErrors;
      });
    }
  };

  // Handle select change
  const handleSelectChange = (value, fieldName) => {
    setFormData(prev => ({
      ...prev,
      [fieldName]: value
    }));
    
    // Clear errors for this field
    if (formErrors[fieldName]) {
      setFormErrors(prev => {
        const newErrors = {...prev};
        delete newErrors[fieldName];
        return newErrors;
      });
    }
  };

  // Handle form submission
  const handleFormSubmit = async (e) => {
    e.preventDefault();
    
    // Validate form
    const errors = {};
    if (!formData.name.trim()) errors.name = "Title is required";
    if (!formData.description.trim()) errors.description = "Description is required";
    if (!formData.category) errors.category = "Category is required";
    if (!formData.location.trim()) errors.location = "Location is required";
    if (!formData.startDate) errors.startDate = "Start date is required";
    if (!formData.endDate) errors.endDate = "End date is required";
    
    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }
    
    try {
      // Convert form data to match the GraphQL mutation structure
      const startDateTime = new Date(`${formData.startDate}T${formData.startTime}`);
      const endDateTime = new Date(`${formData.endDate}T${formData.endTime}`);
      
      // Prepare tags array from comma-separated string
      const tagsArray = formData.tags
        ? formData.tags.split(',').map(tag => tag.trim())
        : [];
      
      // Add perks to tags if provided
      if (formData.perks) {
        tagsArray.push(...formData.perks.split(',').map(perk => perk.trim()));
      }
      
      // Submit mutation
      await createEvent({
        variables: {
          data: {
            name: formData.name,
            description: formData.description,
            category: formData.category.toUpperCase(),
            location: formData.location,
            startDate: startDateTime.toISOString(),
            endDate: endDateTime.toISOString(),
            capacity: parseInt(formData.capacity, 10),
            isPublic: formData.isPublic,
            tags: tagsArray,
            creatorId: userId,
            // Image upload would typically be handled separately or included as base64
          }
        }
      });
      
    } catch (error) {
      console.error("Error submitting form:", error);
      setFormErrors({
        submit: "Failed to create event. Please try again."
      });
    }
  };

  // Get user location when "Near Me" is clicked
  const handleNearMeClick = () => {
    setIsLoadingLocation(true);
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        setUserCoordinates({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude
        });
        setNearMeActive(true);
        setIsLoadingLocation(false);
      }, (error) => {
        console.error("Error getting location:", error);
        alert("Unable to get your location. Please check your browser permissions.");
        setIsLoadingLocation(false);
      });
    } else {
      alert("Geolocation is not supported by this browser.");
      setIsLoadingLocation(false);
    }
  };

  // Function to handle event interactions
  const handleEventAction = (event, action, rsvpStatus = 'GOING') => {
    if (!event || !event.id || !userId) return;
    
    const isExternal = event.__typename === 'ExternalEvent';
    
    switch(action) {
      case 'like':
        if (isExternal) {
          if (event.isLikedByUser) {
            unlikeExternalEvent({ 
              variables: { eventId: event.id, userId },
              refetchQueries: ['GetAllEvents', 'GetEventsNearMe'],
              optimisticResponse: {
                unlikeExternalEvent: {
                  ...event,
                  isLikedByUser: false,
                  likeCount: Math.max(0, (event.likeCount || 0) - 1)
                }
              }
            });
          } else {
            likeExternalEvent({ 
              variables: { eventId: event.id, userId },
              refetchQueries: ['GetAllEvents', 'GetEventsNearMe'],
              optimisticResponse: {
                likeExternalEvent: {
                  ...event,
                  isLikedByUser: true,
                  likeCount: (event.likeCount || 0) + 1
                }
              }
            });
          }
        } else {
          // Internal event logic would go here
          console.log("Internal event like action", event.id);
        }
        break;
      case 'save':
        if (isExternal) {
          if (event.isSavedByUser) {
            unsaveExternalEvent({ 
              variables: { eventId: event.id, userId },
              refetchQueries: ['GetAllEvents', 'GetEventsNearMe'],
              optimisticResponse: {
                unsaveExternalEvent: {
                  ...event,
                  isSavedByUser: false,
                  saveCount: Math.max(0, (event.saveCount || 0) - 1)
                }
              }
            });
          } else {
            saveExternalEvent({ 
              variables: { eventId: event.id, userId },
              refetchQueries: ['GetAllEvents', 'GetEventsNearMe'],
              optimisticResponse: {
                saveExternalEvent: {
                  ...event,
                  isSavedByUser: true,
                  saveCount: (event.saveCount || 0) + 1
                }
              }
            });
          }
        } else {
          // Internal event logic would go here
          console.log("Internal event save action", event.id);
        }
        break;
      case 'rsvp':
        // RSVP logic would go here
        console.log(`RSVP ${rsvpStatus} for event`, event.id);
        break;
      case 'approve':
        // Only for admin users to approve events from universities and companies
        if (isAdmin && (event.creatorType === 'UNIVERSITY' || event.creatorType === 'COMPANY')) {
          // Implement approval mutation here
          console.log("Approving event:", event.id);
        }
        break;
      default:
        break;
    }
  };

  // Combined events from both queries
  const events = nearMeActive && userCoordinates ? 
    (nearMeData?.eventsNearMe || []) : 
    (data?.allEvents || []);
  
  // Calculate total pages
  const totalCount = nearMeActive ? 
    (nearMeData?.eventsNearMe?.length || 0) : 
    (data?.allEvents?.length || 0);
  const totalPages = Math.ceil(totalCount / EVENTS_PER_PAGE) || 1;

  // Format text for display based on role
  const getRoleDisplay = () => {
    const roleName = userRole.charAt(0).toUpperCase() + userRole.slice(1);
    return roleName;
  };

  // Check if page is in loading state
  const isPageLoading = authLoading || !userRole || (loading && !nearMeActive) || (nearMeLoading && nearMeActive);

  // Render loading state if needed
  if (isPageLoading) {
    return (
      <DashboardLayout>
        <DashboardShell>
          <div className="flex justify-center items-center h-64">
            <p className="text-muted-foreground">Loading...</p>
          </div>
        </DashboardShell>
      </DashboardLayout>
    );
  }

  // Main render
  return (
    <DashboardLayout>
      <DashboardShell>
        <div className="flex items-center justify-between">
          <h2 className="text-3xl font-bold tracking-tight"> Events</h2>
          {/* Only show Create Event for universities and companies */}
          {(userRole === 'university' || userRole === 'company') && (
            <Button onClick={() => setIsCreateModalOpen(true)}>Create Event</Button>
          )}
        </div>

        {/* Filters */}
        <div className="grid gap-4 md:grid-cols-[1fr_200px_200px_auto]">
          <div className="relative">
            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
            <Input 
              type="search" 
              placeholder="Search events..." 
              className="w-full pl-8" 
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  refetch(getQueryVariables());
                }
              }}
            />
          </div>
          <Select value={categoryFilter} onValueChange={setCategoryFilter}>
            <SelectTrigger><SelectValue placeholder="Category" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="ALL">All Categories</SelectItem>
              <SelectItem value="HACKATHON">Hackathon</SelectItem>
              <SelectItem value="WORKSHOP">Workshop</SelectItem>
              <SelectItem value="NETWORKING">Networking</SelectItem>
              <SelectItem value="CONFERENCE">Conference</SelectItem>
              <SelectItem value="SUMMIT">Summit</SelectItem>
              <SelectItem value="COMPETITION">Competition</SelectItem>
            </SelectContent>
          </Select>
          <Select value={dateFilter} onValueChange={setDateFilter}>
            <SelectTrigger><SelectValue placeholder="Date" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="ALL">All Dates</SelectItem>
              <SelectItem value="today">Today</SelectItem>
              <SelectItem value="week">This Week</SelectItem>
              <SelectItem value="month">This Month</SelectItem>
            </SelectContent>
          </Select>
          <Button 
            variant={nearMeActive ? "default" : "outline"} 
            className="flex gap-2"
            onClick={handleNearMeClick}
            disabled={isLoadingLocation}
          >
            <MapPin className="h-4 w-4" />
            <span>{isLoadingLocation ? "Loading..." : "Near Me"}</span>
          </Button>
        </div>

        {/* Loading indicators - show when changing filters but page is already loaded */}
        {((loading && !nearMeActive) || (nearMeLoading && nearMeActive)) && (
          <div className="text-center my-8">
            <p>Loading events...</p>
          </div>
        )}
        
        {error && (
          <div className="text-center my-8 text-red-500">
            <p>Error loading events: {error.message}</p>
          </div>
        )}
        
        {isAdmin && (
          <div className="mt-4 mb-2">
            <Badge variant="outline" className="font-medium">
              Admin View: You can approve events from universities and companies
            </Badge>
          </div>
        )}

        {/* Role-specific prompt */}
        {/* <div className="mt-4 mb-2">
          <Badge variant="outline" className="font-medium">
            Viewing events as: {getRoleDisplay()}
          </Badge>
        </div> */}

        {/* Event Cards */}
        <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3 mt-6">
          {events.length > 0 ? (
            events.map((event, index) => (
              <EventCard 
                key={event?.id || index} 
                event={event}
                isAdmin={isAdmin}
                onAction={handleEventAction}
              />
            ))
          ) : (
            !loading && !nearMeLoading && (
              <div className="col-span-3 text-center py-12">
                <p className="text-muted-foreground">No events found. Try adjusting your filters.</p>
              </div>
            )
          )}
        </div>

        {/* Pagination Buttons */}
        {totalPages > 1 && (
          <div className="flex justify-center mt-8 gap-4">
            <Button
              variant="outline"
              onClick={() => setCurrentPage((p) => Math.max(p - 1, 1))}
              disabled={currentPage === 1 || loading || nearMeLoading}
            >
              Previous
            </Button>
            <span className="text-sm text-muted-foreground">Page {currentPage} of {totalPages}</span>
            <Button
              variant="outline"
              onClick={() => setCurrentPage((p) => Math.min(p + 1, totalPages))}
              disabled={currentPage === totalPages || loading || nearMeLoading}
            >
              Next
            </Button>
          </div>
        )}

        {/* Create Event Modal */}
        <Dialog open={isCreateModalOpen} onOpenChange={setIsCreateModalOpen}>
          <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>Create New Event</DialogTitle>
              <DialogDescription>
                Fill out the details to create a new event
              </DialogDescription>
            </DialogHeader>
            
            {formErrors.submit && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded relative mb-4" role="alert">
                <span className="block sm:inline">{formErrors.submit}</span>
              </div>
            )}
            
            <form className="space-y-4" onSubmit={handleFormSubmit}>
              <div className="space-y-2">
                <Label htmlFor="image">Upload Image</Label>
                <Input 
                  id="image" 
                  type="file" 
                  accept="image/*" 
                  onChange={handleImageChange} 
                />
                {imagePreview && (
                  <div className="relative">
                    <img
                      src={imagePreview}
                      alt="Preview"
                      className="mt-2 rounded-md w-full max-h-64 object-cover"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="icon"
                      className="absolute top-2 right-2 bg-black bg-opacity-50 hover:bg-opacity-70 text-white rounded-full p-1"
                      onClick={() => {
                        setImageFile(null);
                        setImagePreview(null);
                      }}
                    >
                      <X className="h-4 w-4" />
                    </Button>
                  </div>
                )}
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="name">Title*</Label>
                <Input 
                  id="name" 
                  placeholder="Event Title"
                  value={formData.name}
                  onChange={handleInputChange} 
                  className={formErrors.name ? "border-red-500" : ""}
                />
                {formErrors.name && (
                  <p className="text-red-500 text-xs mt-1">{formErrors.name}</p>
                )}
              </div>
              
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="startDate">Start Date*</Label>
                  <Input 
                    id="startDate" 
                    type="date"
                    value={formData.startDate}
                    onChange={handleInputChange}
                    className={formErrors.startDate ? "border-red-500" : ""}
                  />
                  {formErrors.startDate && (
                    <p className="text-red-500 text-xs mt-1">{formErrors.startDate}</p>
                  )}
                </div>
                <div className="space-y-2">
                  <Label htmlFor="startTime">Start Time</Label>
                  <Input 
                    id="startTime" 
                    type="time"
                    value={formData.startTime}
                    onChange={handleInputChange}
                  />
                </div>
              </div>
              
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="endDate">End Date*</Label>
                  <Input 
                    id="endDate" 
                    type="date"
                    value={formData.endDate}
                    onChange={handleInputChange}
                    className={formErrors.endDate ? "border-red-500" : ""}
                  />
                  {formErrors.endDate && (
                    <p className="text-red-500 text-xs mt-1">{formErrors.endDate}</p>
                  )}
                </div>
                <div className="space-y-2">
                  <Label htmlFor="endTime">End Time</Label>
                  <Input 
                    id="endTime" 
                    type="time"
                    value={formData.endTime}
                    onChange={handleInputChange}
                  />
                </div>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="location">Location*</Label>
                <Input 
                  id="location" 
                  placeholder="e.g., NYU Tech Center" 
                  value={formData.location}
                  onChange={handleInputChange}
                  className={formErrors.location ? "border-red-500" : ""}
                />
                {formErrors.location && (
                  <p className="text-red-500 text-xs mt-1">{formErrors.location}</p>
                )}
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="category">Category*</Label>
                <Select
                  value={formData.category}
                  onValueChange={(value) => handleSelectChange(value, "category")}
                >
                  <SelectTrigger id="category" className={formErrors.category ? "border-red-500" : ""}>
                    <SelectValue placeholder="Choose category" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="HACKATHON">Hackathon</SelectItem>
                    <SelectItem value="WORKSHOP">Workshop</SelectItem>
                    <SelectItem value="NETWORKING">Networking</SelectItem>
                    <SelectItem value="CONFERENCE">Conference</SelectItem>
                    <SelectItem value="SUMMIT">Summit</SelectItem>
                    <SelectItem value="COMPETITION">Competition</SelectItem>
                  </SelectContent>
                </Select>
                {formErrors.category && (
                  <p className="text-red-500 text-xs mt-1">{formErrors.category}</p>
                )}
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="capacity">Capacity</Label>
                <Input 
                  id="capacity" 
                  type="number" 
                  value={formData.capacity}
                  onChange={handleInputChange}
                  min="1"
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="perks">Perks (comma-separated)</Label>
                <Input 
                  id="perks" 
                  placeholder="e.g., Free swag, food, networking" 
                  value={formData.perks}
                  onChange={handleInputChange}
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="tags">Tags (comma-separated)</Label>
                <Input 
                  id="tags" 
                  placeholder="e.g., technology, career, learning" 
                  value={formData.tags}
                  onChange={handleInputChange}
                />
              </div>
              
              
              
              <div className="space-y-2">
                <Label htmlFor="description">Description*</Label>
                <Textarea
                  id="description"
                  className={`w-full min-h-24 p-2 border rounded-md ${formErrors.description ? "border-red-500" : ""}`}
                  placeholder="Detailed event description..."
                  value={formData.description}
                  onChange={handleInputChange}
                />
                {formErrors.description && (
                  <p className="text-red-500 text-xs mt-1">{formErrors.description}</p>
                )}
              </div>
              
              <DialogFooter>
                <Button 
                  type="button" 
                  variant="outline" 
                  onClick={() => {
                    resetForm();
                    setIsCreateModalOpen(false);
                  }}
                  disabled={createLoading}
                >
                  Cancel
                </Button>
                <Button 
                  type="submit" 
                  disabled={createLoading}
                >
                  {createLoading ? "Creating..." : "Create Event"}
                </Button>
              </DialogFooter>
            </form>
          </DialogContent>
        </Dialog>
      </DashboardShell>
    </DashboardLayout>
  )
}